html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
}

body {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  color: #ffffff;
}
.wrapper {

  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;
}

.container {
  max-width: 1510px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

.list {
  list-style: none;

  padding-left: 0px;
  margin-bottom: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
button {
  outline: none;
  border: none;
}

.active::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  border-radius: 15px;
  display: block;
  background-color: white;
}
.wrapperBackgroundPhoto {
  background-image: linear-gradient(to right, rgba(24, 24, 24, 0.8), rgba(24, 24, 24, 0.8)),
    url(../images/winter.jpg);

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.learnMore {
  position: relative;
}
.learnMore_text {
  font-size: 30px;
  line-height: 37px;
  padding-left: 140px;
  &::before {
    display: block;
    position: absolute;
    content: '';
    top: 19px;
    left: 0;
    width: 129px;
    height: 1px;
    // border: 1px solid #000000;
    background-color: rgb(0, 0, 0, 0.5);
  }
}
